<template>
  <div class="main" :style="{height: pageheight + 'px'}">

    <div class="outLine">
      <div class="title">
        <p>· Antto 安童 ·</p>
        <p>Oriental Tattooist/ IIIustrator/ XAXA Owner</p>
      </div>
      <div class="midbut">
        <span @click="$goto('tattoo')"><p>·</p><b>TATTOO 文身</b><p>·</p></span>
        <span @click="$goto('works')"><p>·</p><b>PAINTING 插画</b><p>·</p></span>
        <span @click="$goto('peripheral')"><p>·</p><b>WORKSHOP周边</b><p>·</p></span>
        <span @click="$goto('choseBook')"><p>·</p><b>BOOKING 预订</b><p>·</p></span>
        <span @click="$goto('Link')"><p>·</p><b>ACCOUNT 联系</b><p>·</p></span>
      </div>
      <div class="homebg">
        <div>
          <span></span>
          <img src="~@/assets/homeBg.jpg" alt="">
        </div>
      </div>

    </div>

    

  </div>
</template>
<script>
import { RouterLink } from 'vue-router';
import Vue from 'vue'
import { Popup } from 'vant';
import 'vant/lib/popup/style';
Vue.use(Popup)
export default {
  name: 'APP',
  data() {
    return {
      show: false,
      pageheight:0
    };
  },
  mounted() {
    this.pageheight=window.innerHeight
    const appDom = document.getElementById('app');
    console.log(this.$store.state.scrollVal)
    if(this.$store.state.scrollVal){
      appDom.scrollTo({
      top: this.$store.state.scrollVal,
      behavior: 'instant'
    });
    }
  },
  methods: {
    copy(){
      const storage = document.createElement('textarea');
      storage.value = 'anttoxaxa';
      this.$refs.reference.appendChild(storage);
      storage.select();
      storage.setSelectionRange(0, 99999);
      document.execCommand('copy');
      this.$refs.reference.removeChild(storage);
      // this.show=false;
        this.$message({
        message:'复制成功！ Copy successfully!',
        duration:1500,
        type:'success'
      });
      // setTimeout(() => {
      //   window.open('weixin://','_blank')
      // }, 1500);
    },
    scrolltop(){
      const appDom = document.getElementById('app');
      console.log(appDom.scrollTop)
      return appDom.scrollTop
    },
    scrollBooking(){
      const appDom = document.getElementById('app');
      const bookingtitle = document.getElementById('booktitle');
      console.log(bookingtitle.scrollTop)
      appDom.scrollTo({
        top:bookingtitle.getBoundingClientRect().top + window.pageYOffset,
        behavior:'smooth'
      })
    },
    toTop() {
      const appDom = document.getElementById('app');
      appDom.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    },
    engpush() {
      const appDom = document.getElementById('app');
      this.$store.state.scrollVal=appDom.scrollTop
      this.$router.push(
        { name: 'booking', params: { ty: 'en' } }
      )
    }
  },
  components: { RouterLink }
}
</script>
<style lang="less" scoped>
@basecolor: #cfcfcf;
@fontcolor: #aaaaaa;

.main {
  width: 750px;
 

 

  .ENG {
    p {
      font-size: 21px !important;
    }
  }

  .booktitle {
    text-align: center;
    font-size: 30px;
    color: @fontcolor;
    margin-bottom: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      font-size: 60px;
    }
  }

  .homebg {
    position: absolute;
    width: 100%;
    height: auto;
    bottom: 0;

    div {
      width: 100%;
      height: 100%;
      position: relative;

      span {
        width: 100%;
        height: 100%;
        display: block;
        background-color: black;
        opacity: 0.5;
        position: absolute;
      }

      img {
        width: 100%;
        height: auto;
        animation: homeBgOp linear 2.5s;
        animation-iteration-count: infinite;
      }
    }

  }

  @keyframes homeBgOp {
    0% {
      opacity: 0.5;
    }

    50% {
      opacity: 0.35;
    }

    100% {
      opacity: 0.5;
    }
  }

  .outLine {
    height: 96vh;
    position: relative;
  }

  .midbut {
    position: absolute;
    top: 20%;
    transform: translate(-50%);
    left: 50%;

    span {
      display: block;
      margin: 0 auto;
      width: 250px;
      height: 50px;
      font-size: 18px;
      color: #aaaaaa;
      margin-bottom: 30px;
      text-align: center;
      line-height: 50px;
      border: 1px solid #aaaaaa;
      display: flex;
      justify-content: space-between;
      padding: 0 20px 0 20px;
      p{
        font-size: 50px;
      }
      b{
        font-weight: normal;
      }
    }
  }

  .title {
    background-color: #282924;
    padding: 10px 0 10px 0;

    p {
      font-size: 16px;
      color: @basecolor;
      &:nth-of-type(1){
        margin-bottom: 4px;
      }
    }
  }

}
</style>
